.player-container{
    min-height: 20vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
}

.time-control{
    width: 50%;
    display: flex;
    input{
        width: 100%;
        padding: 1rem 0rem;
    }
    p{
        padding: 1rem;
    }
}

.play-control{
    width: 30%;
    display: flex;
    align-items: center;
    justify-content: space-between;
    svg{
        cursor: pointer;
    }
}

@media screen and (max-width: 768px){
    .time-control{
        width: 90%;
    }
    .play-control{
        width: 60%;
    }
}