@import url(https://fonts.googleapis.com/css2?family=Lato:wght@400;700&display=swap);
*{padding:0;margin:0;box-sizing:border-box}body{font-family:Lato, sans-serif}h1,h2,h4{color:#353535}h3{font-weight:700}.App{transition:all 0.8s ease}.library-open{margin-left:30%}.player-container{min-height:20vh;display:flex;flex-direction:column;align-items:center;justify-content:space-between}.time-control{width:50%;display:flex}.time-control input{width:100%;padding:1rem 0rem}.time-control p{padding:1rem}.play-control{width:30%;display:flex;align-items:center;justify-content:space-between}.play-control svg{cursor:pointer}@media screen and (max-width: 768px){.time-control{width:90%}.play-control{width:60%}}.song-container{min-height:60vh;display:flex;flex-direction:column;align-items:center;justify-content:center}.song-container img{width:20%;border-radius:50%}.song-container h2{padding:3rem 1rem 1rem 1rem}.song-container h3{font-size:1rem}@media screen and (max-width: 768px){.song-container img{width:60%}}.library{position:fixed;top:0;left:0;height:100%;width:20rem;background-color:whitesmoke;box-shadow:2px 2px 50px #ccc;overflow-y:scroll;transform:translateX(-100%);transition:all 0.8s ease;opacity:0}.open{transform:translateX(0);opacity:1}.title{display:flex;padding:2rem 2rem 1.5rem 2rem;justify-content:space-between}.title .close{display:none}.library-song{display:flex;align-items:center;padding:1.5rem 2rem 1.5rem 2rem;cursor:pointer;transition:all 0.9s ease}.library-song img{width:20%}.library-song:hover{background-color:#ff6c89}.song-description{display:flex;flex-direction:column;padding-left:1rem}.song-description h3{font-size:1rem}.song-description h4{font-size:0.8rem}*{scrollbar-width:thin;scrollbar-color:rgba(155,155,155,0.5) transparent}*::-webkit-scrollbar{width:5px}*::-webkit-scrollbar-track{background:transparent}*::-webkit-scrollbar-thumb{background-color:rgba(155,155,155,0.5);border-radius:20px;border:transparent}.active{background-color:#e92149}@media screen and (max-width: 768px){.library{width:100%}.title .close{display:block}}nav{min-height:10vh;display:flex;justify-content:space-around;align-items:center}nav button{background:transparent;border:none;cursor:pointer;border:2px solid #414141;padding:0.5em;transition:all 0.3s ease}nav button:hover{background-color:#414141;color:white}nav button .music-icon{display:inline-block;margin-left:0.3em}

